
















.UserLayout {
    min-height: 100vh;
    background: url('../assets/img/bgimg1.jpg') no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 0;

    .shade {
        width: 100%;
        height: 100%;
        background: #464646c4;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
    }

    .RouterView {
        width: 400px;
        position: relative;
        top: 0;
        z-index: 1;
    }
}

@primary-color: #1b1b1b;@item-hover-bg: #ddd;@border-radius-base: 2px;@input-hover-border-color: #0d499e;